<template>
  <h1>World</h1>
  <hr />
  <h2>Main Story & Current Event</h2>
  <MDBRow class="row-cols-2 row-cols-md-3 row-cols-lg-4">
    <MDBCol v-for="s in story" :key="s[1]['img']" >
      <MDBCard text="white" bg="dark" class="mb-3 click-card" @click="moveArea(s[0])">
        <MDBCardImg :src="'images/world/' + s[1]['img'] + '.png'" top />
        <MDBCardBody>
          <MDBCardTitle>{{ s[1]["title"] }}</MDBCardTitle>
        </MDBCardBody>
      </MDBCard>
    </MDBCol>
    <MDBCol v-for="s in curEvents" :key="s[1]['img']" >
      <MDBCard text="white" bg="dark" class="mb-3 click-card" @click="moveArea(s[0])">
        <MDBCardImg :src="'images/world/' + s[1]['img'] + '.png'" top />
        <MDBCardBody>
          <MDBCardTitle>{{ s[1]["title"] }}</MDBCardTitle>
        </MDBCardBody>
      </MDBCard>
    </MDBCol>
  </MDBRow>
    <hr />
  <h2>Permanent Events</h2>
  <MDBRow class="row-cols-2 row-cols-md-3 row-cols-lg-4">
    <MDBCol v-for="s in perEvents" :key="s[1]['img']" >
      <MDBCard text="white" bg="dark" class="mb-3 click-card" @click="moveArea(s[0])">
        <MDBCardImg :src="'images/world/' + s[1]['img'] + '.png'" top />
        <MDBCardBody>
          <MDBCardTitle>{{ s[1]["title"] }}</MDBCardTitle>
        </MDBCardBody>
      </MDBCard>
    </MDBCol>
  </MDBRow>
  <hr />
  <h2>Others</h2>
  <MDBRow class="row-cols-2 row-cols-md-3 row-cols-lg-4">
    <MDBCol v-for="s in others" :key="s[1]['img']" >
      <MDBCard text="white" bg="dark" class="mb-3 click-card" @click="moveArea(s[0])">
        <MDBCardImg :src="'images/world/' + s[1]['img'] + '.png'" top />
        <MDBCardBody>
          <MDBCardTitle>{{ s[1]["title"] }}</MDBCardTitle>
        </MDBCardBody>
      </MDBCard>
    </MDBCol>
  </MDBRow>
  <hr />
  <h2>Old Events</h2>
  <MDBRow class="row-cols-2 row-cols-md-3 row-cols-lg-4">
    <MDBCol v-for="s in oldEvents" :key="s[1]['img']" >
      <MDBCard text="white" bg="dark" class="mb-3 click-card" @click="moveArea(s[0])">
        <MDBCardImg :src="'images/world/' + s[1]['img'] + '.png'" top />
        <MDBCardBody>
          <MDBCardTitle>{{ s[1]["title"] }}</MDBCardTitle>
        </MDBCardBody>
      </MDBCard>
    </MDBCol>
  </MDBRow>
</template>

<script>
import { useStore } from "vuex";
import {
  MDBCard,
  MDBCardBody,
  MDBCardTitle,
  // MDBCardText,
  MDBCardImg,
  MDBRow,
  MDBCol,
} from "mdb-vue-ui-kit";
export default {
  components: {
    MDBCard,
    MDBCardBody,
    MDBCardTitle,
    // MDBCardText,
    MDBCardImg,
    MDBRow,
    MDBCol,
  },
  computed: {
    world() {
      return this.$store.getters.getWorld;
    },
    maps() {
      return this.$store.getters.getMaps;
    },
    story() {
      let sto = [];
      for (let zone in this.world.story) {
        if (this.world.story[zone] in this.maps) {
        sto.push([this.world.story[zone], this.maps[this.world.story[zone]]]);
        }
      }
      return sto;
    },
    events() {
            let sto = [];
      for (let zone in this.world.event) {
        if (this.world.event[zone] in this.maps) {
        sto.push([this.world.event[zone], this.maps[this.world.event[zone]], ]);
        }
      }
      return sto;
    },
    curEvents() {
      return this.events.filter((e) => e[1].type == "current")
    },
    perEvents() {
      return this.events.filter((e) => e[1].type == "permanent")
    },
    oldEvents() {
      return this.events.filter((e) => e[1].type == "old")
    },
    others() {
            let sto = [];
            let li = ["Challenge", "Training"]
      for (let zone in li) {
        if (li[zone] in this.maps) {
        sto.push([li[zone], this.maps[li[zone]], ]);
        }
      }
      return sto;
    },
  },
  async setup() {
    const store = useStore();
    await store.dispatch("requestMaps");
  },
  methods: {
    moveArea(a) {
      this.$router.push("/world/" + a);
    }
  }
};
</script>

<style></style>
