<template>
  <MDBContainer>
    <MDBModal
      id="enemyInfo"
      tabindex="-1"
      labelledby="enemyID"
      v-model="enemyInfo"
      scrollable
      size="xl"
      @update:modelValue="updateEmit"
    >
      <MDBModalHeader>
        <MDBModalTitle id="enemyID">
          <h5 id="name">
            {{ info.name }}
            <div style="font-size: 60%">{{ enemy2 }}</div>
          </h5>
        </MDBModalTitle>
      </MDBModalHeader>
      <MDBModalBody>
        <select @change="changeEnemy">
          <option
            v-for="dupe in duplicateEnemies"
            :key="dupe[0]"
            :selected="dupe[0] == enemy2"
          >
            {{ dupe[0] }}
          </option>
        </select>
        <hr />
        <router-link :to="'/enemy/' + enemy2">To Enemy Page</router-link>
        <MDBRow>
          <MDBCol col="6"
            ><div class="image">
              <img :src="'images/profile/' + info.img + '.png'" />
            </div>
            <div class="type">
              <h4 id="type">{{ info.type }}</h4>
            </div>
            <div class="role">
              <h4 id="role">{{ info.role }}</h4>
            </div>
            <div class="LVL">
              LV.
              <input v-model.number="level" type="number" />
            </div>
          </MDBCol>
          <MDBCol col="6">
            <table class="stat-table">
              <tbody>
                <tr>
                  <td>
                    <img src="images/icon_HP2.png" />
                    <div>HP</div>
                  </td>
                  <td>
                    <div class="spec-item" id="HP">{{ hp }}</div>
                  </td>
                </tr>
                <tr>
                  <td>
                    <img src="images/icon_ATK2.png" />
                    <div>ATK</div>
                  </td>
                  <td>
                    <div class="spec-item" id="ATK">{{ atk }}</div>
                  </td>
                  <td>
                    <img src="images/icon_CRT2.png" />
                    <div>CRIT</div>
                  </td>
                  <td>
                    <div class="spec-item" id="CRT">{{ info.CRT }}</div>
                  </td>
                </tr>
                <tr>
                  <td>
                    <img src="images/icon_DEF2.png" />
                    <div>DEF</div>
                  </td>
                  <td>
                    <div class="spec-item" id="DEF">{{ def }}</div>
                  </td>
                  <td>
                    <img src="images/icon_HIT2.png" />
                    <div>ACC</div>
                  </td>
                  <td>
                    <div class="spec-item" id="HIT">{{ info.HIT }}</div>
                  </td>
                </tr>
                <tr>
                  <td>
                    <img src="images/icon_AGI.png" />
                    <div>SPD</div>
                  </td>
                  <td>
                    <div class="spec-item" id="AGI">{{ info.AGI }}</div>
                  </td>
                  <td>
                    <img src="images/icon_DOD2.png" />
                    <div>EVA</div>
                  </td>
                  <td>
                    <div class="spec-item" id="DOD">{{ info.DOD }}</div>
                  </td>
                </tr>
                <tr>
                  <td>Resists</td>
                  <td><img src="images/fire.png" />{{ info.resist[0] }}%</td>
                  <td><img src="images/ice.png" />{{ info.resist[1] }}%</td>
                  <td>
                    <img src="images/electric.png" />{{ info.resist[2] }}%
                  </td>
                </tr>
              </tbody>
            </table>
          </MDBCol>
        </MDBRow>
        <MDBRow>
          <MDBTabs v-model="activeTabId1">
            <!-- Tabs navs -->
            <MDBTabNav>
              <MDBTabItem
                v-for="ski in skill"
                :key="ski.title"
                :href="ski.title"
                :tabId="ski.title"
                ><img
                  :src="
                    'images/SkillIcon/' + ski.img + '_' + ski.type + '.png'
                  "
              /></MDBTabItem>
              <MDBTabItem href="info" tabId="info"
                ><img src="images/info.png" />
              </MDBTabItem>
            </MDBTabNav>
            <!-- Tabs navs -->
            <!-- Tabs content -->
            <MDBTabContent>
              <MDBTabPane
                v-for="ski in skill"
                :key="ski.title"
                :tabId="ski.title"
                class="m-3"
              >
                <skill-tab :data="ski" :atk="atk" />
              </MDBTabPane>
              <MDBTabPane
                href="info"
                tabId="info"
                class="m-3"
                v-html="info.info"
              >
              </MDBTabPane>
            </MDBTabContent>
            <!-- Tabs content -->
          </MDBTabs>
        </MDBRow>

        <MDBRow>
          <MDBCard style="width: 100%; margin: auto">
            <MDBCardHeader>Appeared In</MDBCardHeader>
            <MDBListGroup flush>
              <MDBListGroupItem v-for="appear in appearances" :key="appear[0]">
                <MDBCardTitle>{{ appear[0] }}</MDBCardTitle>
                <MDBCardText>
                  <MDBBtn
                    color="primary"
                    v-for="zone in appear[1]"
                    :key="zone"
                    @click="moveStage(zone[0], zone[1], zone[2])"
                  >
                    {{ zone[3] }}
                  </MDBBtn>
                </MDBCardText>
              </MDBListGroupItem>
            </MDBListGroup>
            <div v-if="appearances.length == 0" class="text-muted">
              No Appearances
            </div>
          </MDBCard>
        </MDBRow>
      </MDBModalBody>
    </MDBModal>
  </MDBContainer>
</template>

<script>
import SkillTab from "./SkillTab.vue";
import {
  MDBCol,
  MDBRow,
  MDBContainer,
  MDBModal,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBBtn,
  MDBTabs,
  MDBTabNav,
  MDBTabContent,
  MDBTabItem,
  MDBTabPane,
  MDBCard,
  MDBCardHeader,
  MDBCardTitle,
  MDBCardText,
  MDBListGroup,
  MDBListGroupItem,
} from "mdb-vue-ui-kit";
import { ref } from "vue";
import { useStore } from "vuex";

export default {
  components: {
    SkillTab,
    MDBCol,
    MDBRow,
    MDBContainer,
    MDBModal,
    MDBModalHeader,
    MDBModalTitle,
    MDBModalBody,
    MDBBtn,
    MDBTabs,
    MDBTabNav,
    MDBTabContent,
    MDBTabItem,
    MDBTabPane,
    MDBCard,
    MDBCardHeader,
    MDBCardTitle,
    MDBCardText,
    MDBListGroup,
    MDBListGroupItem,
  },
  name: "EnemyTab",
  props: {
    enemy: String,
    lvl: Number,
  },
  computed: {
    info() {
      if (this.enemy2 in this.enemies) {
        return this.enemies[this.enemy2];
      }
      return this.enemies[this.enemy];
    },
    hp() {
      return Math.floor(this.info.HP[0] + this.info.HP[1] * (this.level - 1));
    },
    atk() {
      return Math.floor(this.info.ATK[0] + this.info.ATK[1] * (this.level - 1));
    },
    def() {
      return Math.floor(this.info.DEF[0] + this.info.DEF[1] * (this.level - 1));
    },
    skill() {
      let ret = [];
      this.info.skills.forEach((element) => {
        ret.push(this.skills[element]);
      });
      return ret;
    },
    appearances() {
      let enemy;
      if (this.enemy2 in this.enemies) {
        enemy = this.enemy2;
      } else {
        enemy = this.enemy;
      }
      var appeared = [];
      for (var key in this.$store.getters.getMaps) {
        var val = this.$store.getters.getMaps[key];
        let zone = [];
        for (var areas in val["zones"]) {
          val["zones"][areas]["stage"].forEach((stage) => {
            let check = false;
            stage["wave"].forEach((wave) => {
              if (!check) {
                wave["enemylist"].forEach((e2) => {
                  if (!check) {
                    if (e2["index"] == enemy) {
                      check = true;
                      zone.push([key, areas, stage["title"], stage["name"]]);
                    }
                  }
                });
              }
            });
          });
        }
        if (zone.length > 0) {
          appeared.push([val["title"], zone]);
        }
      }
      return appeared;
    },
    duplicateEnemies() {
      var enemylist = this.$store.getters.getEnemies;
      var dupe = [];
      var name = this.info.name;
      for (var key in enemylist) {
        var val = enemylist[key];
        if (val["name"] == name) {
          dupe.push([key, val]);
        }
      }
      return dupe;
    },
  },
  data() {
    return {
      level: this.lvl,
      enemies: this.$store.getters.getEnemies,
      skills: this.$store.getters.getSkills,
      enemy2: this.enemy,
    };
  },
  methods: {
    updateEmit(e) {
      this.$emit("update", e);
    },
    moveStage(area, zone, stage) {
      document.body.classList.remove("modal-open");
      this.$router.replace(
        "/world/" + area + "/" + (parseInt(zone) + 1).toString() + "/" + stage
      );
    },
    changeEnemy(e) {
      this.enemy2 = e.target.value;
    },
  },
  async setup() {
    const store = useStore();
    const enemyInfo = ref(false);
    const activeTabId1 = ref("askill1");
    await store.dispatch("init");
    await store.dispatch("requestMaps");
    return { enemyInfo, activeTabId1 };
  },
  updated() {
    if (this.enemy in this.enemies) {
      this.enemyInfo = true;
    } else {
      this.enemyInfo = false;
    }
    this.enemy2 = this.enemy;
    this.level = this.lvl;
    this.activeTabId1 = ref("askill1");
  },
  mounted() {
    if (this.enemy in this.enemies) {
      this.enemyInfo = true;
    } else {
      this.enemyInfo = false;
    }
    this.activeTabId1 = ref("askill1");
  },
};
</script>

